import React from 'react'

const ReposDisplay = (params) => {

    const {
        name,
        full_name,
        html_url,
        created_at,
        updated_at
    } = params

    return (
        <div className='reposcard'>
            <p>{name}</p>
            <p>{full_name}</p>
            <p>{html_url}</p>
            <p>{created_at}</p>
            <p>{updated_at}</p>
        </div>
    )
}

export default ReposDisplay
